<template>
  <div id="publish">
    <h2>新闻发布</h2>
    <!--loading加载框-->
    <el-container :class="{'show':active}" id="loading"
                  v-loading="active"
                  :element-loading-text="loadingText"
                  element-loading-spinner="el-icon-loading"
                  element-loading-background="rgba(0, 0, 0, 0.8)">
    </el-container>
    <el-form label-position="top" label-width="80px">
      <el-form-item label="分类">
        <el-select v-model="news_data.category" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="标题">
        <el-input v-model="news_data.title"></el-input>
      </el-form-item>
      <el-form-item label="缩略图">
        <div>
          <el-input placeholder="请输入图片网址，或者点击右侧<浏览>按钮上传本地图片" v-model="news_data.thumbnail">
            <template slot="append">
              <label for="thumbnail" style="cursor:pointer">浏览</label>
              <input type="file" style="display: none" id="thumbnail"
                     @change="uploadImg($event, thumbnailCompleteAction)">
            </template>
          </el-input>
        </div>
      </el-form-item>
      <el-form-item label="描述">
        <el-input type="textarea" v-model="news_data.des"></el-input>
      </el-form-item>
      <el-form-item label="内容" style="margin: 0"></el-form-item>
      <div>
        <vue-ueditor-wrap v-model="news_data.content" :config="config"></vue-ueditor-wrap>
      </div>
      <div class="btn-box">
        <el-button type="success" @click="publish">发布</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>

  //引入七牛云sdk
  import * as qiniu from 'qiniu-js'
  //引入ueditor
  import VueUeditorWrap from 'vue-ueditor-wrap'

  export default {
    name: 'publish',
    components: {
      VueUeditorWrap
    },
    data () {
      return {
        news_data: {
          category: '',
          title: '',
          thumbnail: '',
          des: '',
          content: '',
        },
        percentage: 0,
        options: [], // 新闻分类列表
        config: {
          autoHeightEnabled: false,
          // 初始容器高度
          initialFrameHeight: 500,
          // 初始容器宽度
          initialFrameWidth: '100%',
          // 上传文件接口（这个地址是我为了方便各位体验文件上传功能搭建的临时接口，请勿在生产环境使用！！！）
          // serverUrl: '/ueditor/upload/',
          // UEditor 资源文件的存放路径，如果你使用的是 vue-cli 生成的项目，通常不需要设置该选项，vue-ueditor-wrap 会自动处理常见的情况，如果需要特殊配置，参考下方的常见问题2
          UEDITOR_HOME_URL: '/static/ueditor/'
          // 配合最新编译的资源文件，你可以实现添加自定义Request Headers,详情https://github.com/HaoChuan9421/ueditor/commits/dev-1.4.3.3
          // headers: {
          //   access_token: '37e7c9e3fda54cca94b8c88a4b5ddbf3'
          // }
        }
      }
    },
    computed: {
      active: function () {
        return this.percentage !== 0
      },
      loadingText: function () {
        return '正在上传.....' + this.percentage + '%'
      }
    },
    methods: {
      // 获取分类信息
      getCategories: function () {
        this.axios.get('/cms/news_type/').then((response) => {
          if (response.data.code === 200) {
            this.options = response.data.data
          }
        })
      },
      // 上传图片到七牛云
      uploadImg: function (e, func) {
        const self = this
        const img = e.target.files[0]
        // 从服务器获取token
        this.axios.get('/cms/news_img/')
        //接收返回值，进行上传前的配置
          .then((response) => {
            if (response.data.code === 200) {
              const token = response.data.data.token
              const array = img.name.split('.')
              const format = array[array.length - 1]
              const fname = Date.now() + '.' + format
              //限制上传文件的类型
              const putExtra = { 'mimeType': ['image/png', 'image/jpeg', 'image/gif', 'video/mp4'] }
              const config = { region: qiniu.region.z2 }  //指定文件上传到哪个大区，可不传，会自动检测
              // 定义上传阶段的回调函数
              const observer = {
                'next': function (res) {
                  self.uploadNextAction(res.total)
                },
                'error': function (err) {
                  self.uploadErrorAction(err)
                },
                'complete': function (res) {
                  func(res)
                }
              }
              const observable = qiniu.upload(img, fname, token, putExtra, config)
              const subscription = observable.subscribe(observer)  // 上传开始
            }
          }).catch(() => {
          this.$message({
            type: 'error',
            message: '捕获失败'
          })
        })
      },
      //回调-七牛云的上传进度
      uploadNextAction: function (total) {
        this.percentage = Number(total.percent.toFixed())
      },
      // 回调-上传缩略图到七牛云失败
      uploadErrorAction: function (err) {
        this.$message({
          type: 'error',
          message: '图片上传失败'
        })
      },
      // 回调-上传缩略图到七牛云成功
      thumbnailCompleteAction: function (res) {
        const self = this
        this.news_data.thumbnail = 'http://q9mtei3xj.bkt.clouddn.com/' + res.key
        // 初始化进度条并隐藏
        setTimeout(function () {
          self.percentage = 0
        }, 500)
      },
      // 发布新闻
      publish: function () {
        this.$confirm('确定发布该文章吗?', '', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          center: true,
          showClose: false,
          type: 'info'
        }).then(() => {
          let method = null
          if (this.$route.query.id) {
            method = 'put'
          } else {
            method = 'post'
          }
          this.axios({
            method: method,
            url: '/cms/news/',
            data: this.news_data
          }).then((response) => {
            if (response.data.code === 200) {
              //发布成功
              this.news_data = {
                category: '',
                title: '',
                thumbnail: '',
                des: '',
                content: '',
              } //清空页面
              this.$message({
                type: 'success',
                message: '发布成功'
              })
            } else {
              //发布失败
              this.$message({
                type: 'warning',
                message: '发布失败: ' + response.data.message
              })
            }
          })
        }).catch(() => {
          this.$message({
            type: 'error',
            message: '发布失败'
          })
        })

      }
    },
    created: function () {
      this.getCategories()
    },
    mounted () {
      // 通过查询参数判断是修改新闻/发布新闻
      if (this.$route.query.id) {
        const id = this.$route.query.id
        console.log(id)
        // 加载要编辑的新闻信息
        this.axios.get(`/cms/news/?id=${id}`)
          .then((response) => {
            if (response.data.code === 200) {
              response.data.data.category = response.data.data.category.id
              this.news_data = response.data.data
            } else {
              this.$message({
                'type': 'error',
                message: '参数错误'
              })
            }
          })
          .catch(() => {
            this.$message({
              'type': error,
              message: '未知错误'
            })
          })
      }
    },

  }
</script>

<style scoped lang="scss">
  .el-form {
    margin: 30px;
    width: 90%;
  }

  .btn-box {
    width: 100%;
    height: 30px;
    margin-bottom: 50px;
  }

  .el-button {
    float: right;
    margin-top: 20px;
  }

  .el-select {
    /*width: 500px;*/
  }

  #loading {
    height: 120px;
    width: 120px;
    position: fixed;
    z-index: 9999;
    display: none;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .show {
    display: block !important;
  }


</style>
