<template>
  <div id="publish">
    <h2>课程发布</h2>
    <!--loading加载框-->
    <el-container :class="{'show':active}" id="loading"
                  v-loading="active"
                  :element-loading-text="loadingText"
                  element-loading-spinner="el-icon-loading"
                  element-loading-background="rgba(0, 0, 0, 0.8)">
    </el-container>
    <el-form label-position="top" label-width="80px">
      <el-form-item label="分类">
        <el-select v-model="course_data.category" placeholder="请选择">
          <el-option
            v-for="item in categoryOptions"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="教师">
        <el-select v-model="course_data.teacher" placeholder="请选择">
          <el-option
            v-for="item in teacherOptions"
            :key="item.id"
            :label="item.username"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="名称">
        <el-input v-model="course_data.title"></el-input>
      </el-form-item>
      <el-form-item label="封面">
        <div>
          <el-input placeholder="请输入图片网址，或者点击右侧<浏览>按钮上传本地图片" v-model="course_data.cover_url">
            <template slot="append">
              <label for="thumbnail" style="cursor:pointer">浏览</label>
              <input type="file" style="display: none" id="thumbnail"
                     @change="uploadImg($event, thumbnailCompleteAction)">
            </template>
          </el-input>
        </div>
      </el-form-item>
      <el-form-item label="简介">
        <el-input type="textarea" v-model="course_data.profile"></el-input>
      </el-form-item>
      <el-form-item label="价格/元">
        <el-input v-model="course_data.price"></el-input>
      </el-form-item>
      <p>选填</p>
      <el-form-item label="时长/min">
        <el-input v-model="course_data.duration"></el-input>
      </el-form-item>
      <el-form-item label="视频地址">
        <el-input placeholder="请输入视频网址" v-model="course_data.video_url"></el-input>
      </el-form-item>
      <div class="btn-box">
        <el-button type="success" @click="publish">保存</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>

  //引入七牛云sdk
  import * as qiniu from 'qiniu-js'

  export default {
    name: 'coursePublish',
    components: {},
    data () {
      return {
        course_data: {
          title: '',
          teacher: '',
          category: '',
          price: '',
          cover_url: '',
          profile: '',
          video_url: '',
          duration: ''
        },
        serverURL: '/cms/course_detail/',
        percentage: 0,
        categoryOptions: [], // 课程分类列表
        teacherOptions: [], // 教师列表
      }
    },
    computed: {
      active: function () {
        return this.percentage !== 0
      },
      loadingText: function () {
        return '正在上传.....' + this.percentage + '%'
      }
    },
    methods: {
      // 获取分类信息
      getCategories: function () {
        this.axios.get('/cms/course_type/').then((response) => {
          if (response.data.code === 200) {
            this.categoryOptions = response.data.data
          }
        })
      },
      // 获取教师列表
      getTeacherList: function () {
        this.axios.get('/cms/teacher_list/').then((response) => {
          if (response.data.code === 200) {
            this.teacherOptions = response.data.data
          }
        })
      },
      // 上传图片到七牛云
      uploadImg: function (e, func) {
        const self = this
        const img = e.target.files[0]
        // 从服务器获取token
        this.axios.get('/cms/news_img/')
        //接收返回值，进行上传前的配置
          .then((response) => {
            if (response.data.code === 200) {
              const token = response.data.data.token
              const array = img.name.split('.')
              const format = array[array.length - 1]
              const fname = Date.now() + '.' + format
              //限制上传文件的类型
              const putExtra = { 'mimeType': ['image/png', 'image/jpeg', 'image/gif', 'video/mp4'] }
              const config = { region: qiniu.region.z2 }  //指定文件上传到哪个大区，可不传，会自动检测
              // 定义上传阶段的回调函数
              const observer = {
                'next': function (res) {
                  self.uploadNextAction(res.total)
                },
                'error': function (err) {
                  self.uploadErrorAction(err)
                },
                'complete': function (res) {
                  func(res)
                }
              }
              const observable = qiniu.upload(img, fname, token, putExtra, config)
              const subscription = observable.subscribe(observer)  // 上传开始
            }
          }).catch(() => {
          this.$message({
            type: 'error',
            message: '捕获失败'
          })
        })
      },
      //回调-七牛云的上传进度
      uploadNextAction: function (total) {
        this.percentage = Number(total.percent.toFixed())
      },
      // 回调-上传缩略图到七牛云失败
      uploadErrorAction: function (err) {
        this.$message({
          type: 'error',
          message: '图片上传失败'
        })
      },
      // 回调-上传缩略图到七牛云成功
      thumbnailCompleteAction: function (res) {
        const self = this
        this.course_data.cover_url = 'http://q9mtei3xj.bkt.clouddn.com/' + res.key
        // 初始化进度条并隐藏
        setTimeout(function () {
          self.percentage = 0
        }, 500)
      },
      // 发布课程
      publish: function () {
        this.$confirm('确定发布该课程吗?', '', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          center: true,
          showClose: false,
          type: 'info'
        }).then(() => {
            let method = null
            if (this.$route.query.id !== 0) {
              method = 'put'
            } else {
              method = 'post'
            }
            this.axios({
              method: method,
              url: this.serverURL,
              data: this.course_data
            }).then((response) => {
              if (response.data.code === 200) {
                //发布成功
                this.course_data = {
                  title: '',
                  teacher: [],
                  category: [],
                  price: '',
                  cover_url: '',
                  profile: '',
                  video_url: '',
                  duration: ''
                }
                //清空页面
                this.$message({
                  type: 'success',
                  message: '发布成功'
                })
              } else {
                //发布失败
                this.$message({
                  type: 'warning',
                  message: '发布失败: ' + response.data.message
                })
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'error',
            message: '发布失败'
          })
        })

      }
    },
    created: function () {
      this.getCategories()
      this.getTeacherList()
    }
    ,
    mounted () {
      // 通过查询参数判断是修改课程/新增课程
      if (this.$route.query.id !== 0) {
        const id = this.$route.query.id
        // 加载要编辑的新闻信息
        this.axios.get(this.serverURL + `?id=${id}`)
          .then((response) => {
            if (response.data.code === 200) {
              response.data.data.category = response.data.data.category.id
              response.data.data.teacher = response.data.data.teacher.id
              response.data.data.price = response.data.data.price.toFixed(1)
              this.course_data = response.data.data
            } else {
              this.$message({
                'type': 'error',
                message: '参数错误'
              })
            }
          })
          .catch(() => {
            this.$message({
              'type': error,
              message: '未知错误'
            })
          })
      }
    },
    beforeRouteUpdate: function (to, from, next) {
      if (to.query.id !== 0) {
        const id = to.query.id
        // 加载要编辑的新闻信息
        this.axios.get(this.serverURL + `?id=${id}`)
          .then((response) => {
            if (response.data.code === 200) {
              this.course_data = response.data.data
            } else {
              this.$message({
                'type': 'error',
                message: '参数错误'
              })
            }
          })
          .catch(() => {
            this.$message({
              'type': 'error',
              message: '未知错误'
            })
          })
      }
      else{
        this.course_data = {
          title: '',
          teacher: [],
          category: [],
          price: '',
          cover_url: '',
          profile: '',
          video_url: '',
          duration: ''
        }
      }
      next()
    }
  }
</script>

<style scoped lang="scss">
  .el-form {
    margin: 30px;
    width: 90%;
  }

  .btn-box {
    width: 100%;
    height: 30px;
    margin-bottom: 50px;
  }

  .el-button {
    float: right;
  }

  .el-select {
    /*width: 500px;*/
  }

  #loading {
    height: 120px;
    width: 120px;
    position: fixed;
    z-index: 9999;
    display: none;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .show {
    display: block !important;
  }

  .quill-editor {
    .ql-container {
      min-height: 300px !important;
    }
  }

</style>
