<template>
  <div id="publish">
    <h2>新建教师</h2>
    <!--loading加载框-->
    <el-container :class="{'show':active}" id="loading"
                  v-loading="active"
                  :element-loading-text="loadingText"
                  element-loading-spinner="el-icon-loading"
                  element-loading-background="rgba(0, 0, 0, 0.8)">
    </el-container>
    <el-form label-position="top" label-width="80px">
      <el-form-item label="名称">
        <el-input v-model="teacher_data.username"></el-input>
      </el-form-item>
      <el-form-item label="头像">
        <div>
          <el-input placeholder="请输入图片网址，或者点击右侧<浏览>按钮上传本地图片" v-model="teacher_data.avatar">
            <template slot="append">
              <label for="avatar" style="cursor:pointer">浏览</label>
              <input type="file" style="display: none" id="avatar"
                     @change="uploadImg($event, avatarCompleteAction)">
            </template>
          </el-input>
        </div>
      </el-form-item>
      <el-form-item label="职称">
        <el-input v-model="teacher_data.jobtitle"></el-input>
      </el-form-item>
      <el-form-item label="简介">
        <el-input type="textarea" v-model="teacher_data.profile"></el-input>
      </el-form-item>
      <div class="btn-box">
        <el-button type="success" @click="publish">保存</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>

  //引入七牛云sdk
  import * as qiniu from 'qiniu-js'

  export default {
    name: 'edit',
    components: {},
    data () {
      return {
        teacher_data: {
          title: '',
          avatar: '',
          username: '',
          profile: ''
        },
        serverURL: '/cms/teacher/',
        percentage: 0,
      }
    },
    computed: {
      active: function () {
        return this.percentage !== 0
      },
      loadingText: function () {
        return '正在上传.....' + this.percentage + '%'
      }
    },
    methods: {
      // 上传图片到七牛云
      uploadImg: function (e, func) {
        const self = this
        const img = e.target.files[0]
        // 从服务器获取token
        this.axios.get('/cms/news_img/')
        //接收返回值，进行上传前的配置
          .then((response) => {
            if (response.data.code === 200) {
              const token = response.data.data.token
              const array = img.name.split('.')
              const format = array[array.length - 1]
              const fname = Date.now() + '.' + format
              //限制上传文件的类型
              const putExtra = { 'mimeType': ['image/png', 'image/jpeg', 'image/gif', 'video/mp4'] }
              const config = { region: qiniu.region.z2 }  //指定文件上传到哪个大区，可不传，会自动检测
              // 定义上传阶段的回调函数
              const observer = {
                'next': function (res) {
                  self.uploadNextAction(res.total)
                },
                'error': function (err) {
                  self.uploadErrorAction(err)
                },
                'complete': function (res) {
                  func(res)
                }
              }
              const observable = qiniu.upload(img, fname, token, putExtra, config)
              const subscription = observable.subscribe(observer)  // 上传开始
            }
          }).catch(() => {
          this.$message({
            type: 'error',
            message: '捕获失败'
          })
        })
      },
      //回调-七牛云的上传进度
      uploadNextAction: function (total) {
        this.percentage = Number(total.percent.toFixed())
      },
      // 回调-上传缩略图到七牛云失败
      uploadErrorAction: function (err) {
        this.$message({
          type: 'error',
          message: '图片上传失败'
        })
      },
      // 回调-上传缩略图到七牛云成功
      avatarCompleteAction: function (res) {
        const self = this
        this.teacher_data.avatar = 'http://q9mtei3xj.bkt.clouddn.com/' + res.key
        // 初始化进度条并隐藏
        setTimeout(function () {
          self.percentage = 0
        }, 500)
      },
      // 新增教师
      publish: function () {
        this.$confirm('确定保存该教师信息吗?', '', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          center: true,
          showClose: false,
          type: 'info'
        }).then(() => {
          let method = null
          if (this.$route.query.id !== 0) {
            method = 'put'
          } else {
            method = 'post'
          }
          this.axios({
            method: method,
            url: this.serverURL,
            data: this.teacher_data
          }).then((response) => {
            if (response.data.code === 200) {
              //发布成功
              this.teacher_data = {
                title: '',
                avatar: '',
                username: '',
                profile: ''
              } //清空页面
              this.$message({
                type: 'success',
                message: '发布成功'
              })
            } else {
              //发布失败
              this.$message({
                type: 'warning',
                message: '发布失败: ' + response.data.message
              })
            }
          })
        }).catch(() => {
          this.$message({
            type: 'error',
            message: '发布失败'
          })
        })

      }
    },
    mounted () {
      // 通过查询参数判断是修改教师信息/新增教师
      if (this.$route.query.id !== 0) {
        const id = this.$route.query.id
        // 加载要编辑的新闻信息
        this.axios.get(this.serverURL + `?id=${id}`)
          .then((response) => {
            if (response.data.code === 200) {
              this.teacher_data = response.data.data
            } else {
              this.$message({
                'type': 'error',
                message: '参数错误'
              })
            }
          })
          .catch(() => {
            this.$message({
              'type': 'error',
              message: '未知错误'
            })
          })
      }
    },
    beforeRouteUpdate: function (to, from, next) {
      if (to.query.id !== 0) {
        const id = to.query.id
        // 加载要编辑的新闻信息
        this.axios.get(this.serverURL + `?id=${id}`)
          .then((response) => {
            if (response.data.code === 200) {
              this.teacher_data = response.data.data
            } else {
              this.$message({
                'type': 'error',
                message: '参数错误'
              })
            }
          })
          .catch(() => {
            this.$message({
              'type': 'error',
              message: '未知错误'
            })
          })
      }
      else{
        this.teacher_data = {
        title: '',
        avatar: '',
        username: '',
        profile: ''
      }
      }
      next()
    }

  }
</script>

<style scoped lang="scss">
  .el-form {
    margin: 30px;
    width: 90%;
  }

  .btn-box {
    width: 100%;
    height: 30px;
    margin-bottom: 50px;
  }

  .el-button {
    float: right;
  }

  .el-select {
    /*width: 500px;*/
  }

  #loading {
    height: 120px;
    width: 120px;
    position: fixed;
    z-index: 9999;
    display: none;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .show {
    display: block !important;
  }

  .quill-editor {
    .ql-container {
      min-height: 300px !important;
    }
  }

</style>
