<template>
  <div id="category">
    <h2>新闻分类</h2>
    <el-button class="category-add-btn" type="success" size="small" @click="addCategory">添加分类</el-button>
    <el-table
      :data="tableData"
      style="width: 60%">
      <el-table-column
        label="名称"
        width="180">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="内容数量"
        width="180">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.news_count }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.row.id, scope.row.name, scope.$index)">编辑
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.row.id, scope.$index)">删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: 'category',
    data () {
      return {
        tableData: []
      }
    },
    methods: {
      //向服务器请求该页面初始数据,也可用于刷新页面
      getInitData: function () {
        this.axios.get('/cms/news_type/').then((response) => {
          if (response.data.code === 200) {
            this.tableData = response.data.data
          }
        })
      },
      //定义添加分类的函数
      addCategory () {
        this.$prompt('名称', '添加分类', {
          confirmButtonText: '添加',
          cancelButtonText: '取消',
          inputValidator: value => value.length >= 2 ? true : '不能少于两个字符'
        }).then(({ value }) => {
          // 发送新分类名称到服务器，请求创建新分类
          this.axios.post('/cms/news_type/', { 'name': value }).then((response) => {
            // 后台创建分类成功
            if (response.data.code === 200) {
              this.$message({
                type: 'success',
                message: '分类添加成功: ' + value
              })
              this.tableData.push(response.data.data)  // 更新数据
            }
            // 后台创建分类失败
            else {
              this.$message({
                type: 'error',
                message: '分类添加失败: ' + response.data.message
              })
            }
          })
        })
        // 前台请求异常
          .catch(() => {
            this.$message({
              type: 'info',
              message: '取消输入'
            })
          })
      },
      // 定义修改分类的函数
      handleEdit (id, name, index) {
        this.$prompt('新名称', '修改分类', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputValue: name,
        }).then(({ value }) => {
          this.axios.put('/cms/news_type/', {
            'name': value,
            'type_id': id
          }).then((response) => {
            //  修改分类成功
            if (response.data.code === 200) {
              this.$message({
                type: 'success',
                message: '分类修改成功: ' + value
              })
              this.tableData[index].name = response.data.data.name //更新数据
            }
            //修改分类失败
            else {
              this.$message({
                type: 'error',
                message: '分类修改失败: ' + response.data.message
              })
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          })
        })
      },
      // 定义删除分类的函数
      handleDelete: function (id, index) {
        this.$confirm('您确定要删除该分类吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.axios({
            url: '/cms/news_type/',
            method: 'delete',
            data: { 'type_id': id }
          }).then((response) => {
            //删除成功
            if (response.data.code === 200) {
              this.$message({
                type: 'success',
                message: '分类删除成功'
              })
              this.tableData.splice(index, 1) //更新页面数据
            }else{
              //删除失败
              this.$message({
                type: 'error',
                message: '分类删除失败'
              })
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      }
    },
    created: function () {
      this.getInitData()
    }
  }
</script>

<style scoped>
  .category-add-btn {
    margin-left: 50px;
    margin-top: 20px;
  }

  .el-table {
    margin: 20px 50px 50px 50px;
  }
</style>
