<template>
  <div id="newsList">
    <h2>教师列表</h2>
    <el-button class="category-add-btn" type="success" size="small" @click="addTeacher">添加教师</el-button>
    <el-table
      :data="tableData"
      style="width: 70%">
      <el-table-column
        label="名称"
        width="180"
        align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.username }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="职称"
        width="180"
        align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.jobtitle }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="课程数量"
        width="180"
        align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.course_count }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.row.id)">编辑
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row.id)">删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: 'teacherList',
    data: function () {
      return {
        tableData: [],
        serverURL: '/cms/teacher_list/'
      }
    },
    methods: {
      // 获取教师列表
      getInitData: function () {
        this.axios.get(this.serverURL).then((response) => {
          if (response.data.code === 200) {
            // 请求成功
            this.tableData = response.data.data
          }
        })
      },
      // 删除指定教师
      handleDelete: function (index, id) {
        this.$confirm('您确定要删除该教师吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            this.axios({
              url: '/cms/teacher/',
              method: 'delete',
              data: { 'id': id }
            })
              .then((response) => {
                //删除成功
                if (response.data.code === 200) {
                  this.$message({
                    type: 'success',
                    message: '删除成功'
                  })
                  this.tableData.splice(index, 1) //更新页面数据
                } else {
                  //删除失败
                  this.$message({
                    type: 'error',
                    message: '删除失败：' + response.data.message
                  })
                }
              })
          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            })
          })
      },
      handleEdit: function (id) {
        this.$router.push({name: 'teacher', query: {id: id}})
      },
      addTeacher: function () {
        this.$router.push({name: 'teacher', query: {id: 0}})
      }
    },
    created: function () {
      this.getInitData()
    }
  }
</script>

<style scoped lang="scss">
   .category-add-btn {
    margin-left: 50px;
    margin-top: 20px;
  }
  .el-table {
    margin: 20px 50px 50px 50px;
  }
</style>
