<template>
  <div id="newsList">
    <h2>新闻列表</h2>
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        label="日期"
        width="180"
        align="center">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.pub_date }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="作者"
        width="180"
        align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.author }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="标题"
        width="600"
        align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.title }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="分类"
        width="180"
        align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.category }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.row.id)">编辑
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row.id)">删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: 'newsList',
    data: function () {
      return {
        tableData: []
      }
    },
    methods: {
      // 获取新闻列表
      getInitData: function () {
        this.axios.get('/cms/news_list/').then((response) => {
          if (response.data.code === 200) {
            // 请求成功
            response.data.data.forEach(function (item) {
              // 处理发布时间
              item.pub_date = item.pub_date.split('T')[0]
            })
            this.tableData = response.data.data
          }
        })
      },
      // 删除指定新闻
      handleDelete: function (index, id) {
        this.$confirm('您确定要删除该新闻吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            this.axios({
              url: '/cms/news/',
              method: 'delete',
              data: { 'id': id }
            })
              .then((response) => {
                //删除成功
                if (response.data.code === 200) {
                  this.$message({
                    type: 'success',
                    message: '新闻删除成功'
                  })
                  this.tableData.splice(index, 1) //更新页面数据
                } else {
                  //删除失败
                  this.$message({
                    type: 'error',
                    message: '新闻删除失败'
                  })
                }
              })
          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            })
          })
      },
      handleEdit: function (id) {
        this.$router.push({name: 'publish', query: { id: id }})
      }
    },
    created: function () {
      this.getInitData()
    }
  }
</script>

