import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

import Index from '../components/index'
import newsCategory from '../components/news/category'
import newsPublish from '../components/news/publish'
import newsList from '../components/news/newsList'

import courseCategory from '../components/course/category'
import courseList from '../components/course/courseList'
import course from '../components/course/coursePublish'

import teacher from '../components/teachers/edit'
import teacherList from '../components/teachers/teacherList'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect: '/index/',
    children: [
      {
        path: 'index/', name: 'index', component: Index
      },
      {
        path: 'category/', name: 'category', component: newsCategory
      },
      {
        path: 'publish/', name: 'publish', component: newsPublish
      },
      {
        path: 'news_list/', name: 'newsList', component: newsList
      },
      {
        path: 'course_category/', name: 'courseCategory', component: courseCategory
      },
      {
        path: 'teacher/', name: 'teacher', component: teacher
      },
      {
        path: 'teacher_list', name: 'teacher_list', component: teacherList
      },
      {
        path: 'course', name: 'course', component: course
      },
      {
        path: 'course_list', name: 'course_list', component: courseList
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'hash',
  routes
})

export default router
